@import 'npm:normalize.css/normalize.css';
*{line-height:1.2;margin:0;padding:0;}
// *{outline:1px solid rgba(255,0,0,0.2)}
html{
    position:relative;
    overflow:hidden;
    width:100vw;height:100vh;
    font-family:arial;font-size:10px;
}
body{
    font-size:1.6rem;
    text-align:center;
    position:relative;
    overflow:auto;
    width:100vw;height:100vh;
    background-color:#eee;
    overflow-x:hidden;
    transition:background-color 1s ease;
    display:flex;
    flex-direction:column;
    align-items:center;
}
body.backend-switch{
    background-color:#555;
    transition:background-color 1s ease;
}
body.backend-switch-2{
    background-color:rgb(255, 231, 224);
    transition:background-color 1s ease;
}

a{
    display:inline-block;
    color:rgba(0,0,110,1);
    border-bottom:2px dashed rgba(0, 0, 110, 0.5);
    text-decoration:none;
}

.backtosimple{
    color:#000;
    border-bottom:2px dashed rgba(0, 0, 0, 0.4);

    @media(min-width:768px){
        align-self:flex-start;margin-left:20px;
    }
}

.descript-wrapper{
    height: auto; /* Let this div take its content height */
    padding: 20px; /* Optional padding */
    display:flex;
    justify-content: center;
    align-items:center;
    flex-direction:column;
}

.descript-container{
    color:#555;
    transition: color 2s ease;
    @media (min-width: 768px){
        width:70vw;
        max-width:800px;
        font-size:20px;
        padding-top:0px;
    }
}

.descript-switcher{
    display:flex;
    color:black;
    transition: color 0.1s ease;
}

.descript-switcher-container{
    border:solid 2px black;
    display:flex;
    flex-direction: row;
    position:relative;
    width:calc(40px * 3);
    border-radius: 15px;
    overflow:hidden;
    margin-top:15px;
    transition: all 0.5s ease;
}
.descript-switcher-btn{
    width:30px;padding:5px;
    cursor:pointer;
}
.descript-switcher-btn-select-overlay{
    height:100%;width:40px;
    position:absolute;
    background-color:#888;
    transition: all 0.3s ease-in;
    left:0;top:0;
    border-radius: 10px;
    z-index:-1;
}

.backend-switch{
    .descript-switcher{
        color:#ddd;
        transition: all 0.1s ease;
    }
    .descript-switcher-container{
        border-color:#ddd;
        transition: all 1s ease;
    }
    .descript-switcher-btn-select-overlay{
        left:40px;
        background-color:#888;
        transition: all 0.3s ease-in;
    }
}

.backend-switch-2 {
    .descript-switcher-btn-select-overlay{
        left:80px;
    }
}

.backend-switch .descript-container{
    color:#ccc;
    transition: color 2s ease;
}

.list-wrapper{
    position:relative;
    width:100vw;
    height:100vh;
    display:flex;
    align-items:center;
    flex-direction:column;
}
.list-container{
    position:relative;
    width:90vw;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(130px, 1fr));
    grid-template-rows: auto auto 150px auto;
    gap:10px;
    
    @media (min-width: 768px){
        width:70vw;
        gap:20px;
        grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
        grid-template-rows: auto auto 150px auto;
    }
}
.list-close,.list-next,.list-prev{width:0px;overflow:hidden;border-radius: 15px;}
.list-lightbox{transition:all 0.5s;pointer-events:none;position:fixed;background-color:rgba(0,0,0,0);width:100%;height:100%;top:0;left:0;}
// [data-page="1"] .list-prev, [data-page="16"] .list-next{
//     opacity:0;
//     pointer-events:none;
// }
.item-wrapper{
    position:relative;
    height:100%;
    min-height:150px;
    border-radius:15px;
    overflow:hidden;
    max-width:500px;
    box-shadow: 1px 2px 6px rgba(0, 0, 0, 0.35);
    
    &.selected{
        outline-width:5px;
        position:fixed;
        z-index:9;
        width:82vw;
        max-height:73vh;
        overflow:auto;
        border-radius:15px;
        top:50%;bottom:50%;
        left:50%;right:50%;
        transform: translate(-50%, -50%);
        box-shadow: 1px 1px 15px rgba(255, 255, 255, 0.3);

        .item-image-wrapper{
            flex-grow:1;
            overflow:hidden;
            max-height:40%;

            @media (min-width: 768px){
                height:100%;
            }
        }

        .item-container{
            background-color:#eee;
            display:flex;
            flex-direction:column;
        }

        .item-description{
            padding:10px 25px;
            text-align: left;
            flex-shrink:1;
            height:inherit;
            position:relative;
            font-size:inherit;
            
            h2{
                font-size:20px;
                margin:0;padding:0 0 5px 0;
                width:auto;
                color:black;
                background-color:inherit;
            }

            @media (max-width: 768px){
                font-size:12px;
                h2{font-size:14px;}
            }
        }

        ~.list-close, ~.list-prev, ~.list-next{
            position:fixed;
            bottom:0px;right:50%;left:50%;
            transform: translate(-50%, -50%);
            color:white;
            z-index:100;
            background: linear-gradient(45deg, #52299e, #5f4fe7);
            color:rgb(188, 247, 255);
            box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
            padding:10px;
            width:25px;
            cursor:pointer;
        }

        ~.list-prev,~.list-next{
            font-size:2.5rem;
            padding:4px 10px 6px 10px;
        }
        
        // Variant 1
        ~.list-prev{margin-left:-100px;}
        ~.list-next{margin-left:100px;}

        // Variant 2
        // ~.list-close{margin-left:-100px;}
        // ~.list-prev{margin-left:50px;}
        // ~.list-next{margin-left:100px;}

        ~.list-lightbox{
            background-color:rgba(0,0,10,0.9);
            z-index:8;
            pointer-events:all;
        }
    }
}

.item-description{
    overflow:hidden;
    position:absolute;
    top:0;left:0;
    font-size:0;

    h2{
        color:rgb(223, 223, 223);
        z-index:1;
        font-size:12px;
        max-width:100%;
        margin:5px;
        box-sizing: border-box;
        background-color:rgba(0,0,0,0.6);
        border-radius: 10px;
        position: relative;
        padding:4px 8px;
        text-align:left;
    }
}
.item-container{
    box-sizing: border-box;
    position:relative;
    height:100%;
}
.item-image-wrapper{
    display:flex;
    height:100%;
}
.item-image{
    width:100%;
    object-fit: cover;
    object-position: center center;
}

.item-backend, .item-ux{
    height:0;overflow:hidden;
    transition: height 0.5s ease-in-out;
}
.item-container:not(.item-backend){
    transition: height 0.5s ease-in-out;
}

.backend-switch{
    .item-container:not(.item-backend){
        height:0;overflow:hidden;
        transition: height 0.5s ease-in-out;
    }

    .item-backend{
        height:100%;
        transition: height 0.5s ease-in-out;
    }
}

.backend-switch-2{
    .item-container:not(.item-ux){
        height:0;overflow:hidden;
        transition: height 0.5s ease-in-out;
    }

    .item-ux{
        height:100%;
        transition: height 0.5s ease-in-out;
    }
}

.list-switch{
    position:fixed;
    top:36vw;bottom:0;
    margin-top:auto;margin-bottom:auto;
    height:18px;
    right:-45px;
    transform: translate(-50%, -50%);
    background: linear-gradient(45deg, #131313, #494949);
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.5);
    padding:15px 22px 15px 15px;
    width:auto;
    cursor:pointer;
    border-radius: 15px;
    transition:all 0.3s ease;
    font-size:3rem;
    z-index:9;
    display:flex;
    justify-content: center;
    align-items:center;

    div{
        width:0;
        overflow:hidden;
        color:rgb(216, 216, 216);
        display:flex;
        align-items:center;
        justify-content: center;
        line-height:1;
        transform: rotate(90deg);
    }

    @media (min-width: 768px) {
        height:60px;
        right:-80px;left:auto;
        padding: 7px 40px 7px 15px;
        font-size:45px;
        top:0;bottom:0;
        box-sizing: border-box;
    }
}

.backend-noswitch .list-switch{
    .list-switch-be{
        width:100%;
    }
}

.backend-switch .list-switch{
    background: linear-gradient(45deg, #e9e9e9, #ffffff);
    transition:all 0.3s ease;

    .list-switch-ux{
        width:100%;
        color:rgb(0, 0, 0);
    }
}
.backend-switch-2 .list-switch{
    background: linear-gradient(45deg, #ffb9b9, #ffefef);
    .list-switch-fe{
        width:100%;
        color:rgb(122, 37, 37);
    }
}

.selected ~.list-switch{
    padding:15px 8px 15px 10px;
    right:-10px;bottom:0px;
    transition:all 0.3s ease;

    @media (min-width: 768px) {
        padding:5px 15px 5px 17px;
        right:-315px;left:auto;
        margin-left:50%;margin-right:50%;
        top:0;
        bottom:0;
    }
}